//Angular imports
import { Routes, RouterModule } from "@angular/router";
import { ModuleWithProviders } from "@angular/core";

//Components
import { HomeComponent } from "./home/home.component";
import { LoginComponent } from "./login/login.component";
import { RegisterComponent } from "./register/register.component";
import { AdminComponent } from './admin/admin.component';
import { LandingComponent } from './landing/landing.component';
import { SourceFilesComponent } from './source-files/source-files.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { SavedFilesComponent } from './saved-files/saved-files.component';

//Guards
import { AuthGuard } from "./_guards/auth.guard";

const appRoutes: Routes = [
    { path: '', component: LandingComponent},
    { path: 'dashboard', component: DashboardComponent, canActivate:Â [AuthGuard],
        children: [
            { path: '', component: HomeComponent, pathMatch:'full' },
            { path: 'files', component: SourceFilesComponent},
            { path: 'saved', component: SavedFilesComponent }
        ]
    },
    { path: 'login', component: LoginComponent},
    { path: 'register', component: RegisterComponent},
    { path: '**', redirectTo: ''}
];

export const appRouting: ModuleWithProviders = RouterModule.forRoot(appRoutes, {useHash: true});
